import React from "react";
import styled from "styled-components";
// import PropTypes from "prop-types";

export class SkillMap extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <React.Fragment>
        <Svg>
          <rect
            x="100"
            y="100"
            width="100"
            height="100"
            fill="red"
            stroke="lime"
            strokeWidth="5px"
          ></rect>
          <DiamondRectangle />
          <Text>Tombo</Text>
        </Svg>
      </React.Fragment>
    );
  }
}

const Svg = styled.svg.attrs(() => ({
  width: "100%",
  height: "100%",
  viewBox: "0 0 100% 100%",
  fill: "none",
  xmlns: "http://www.w3.org/2000/svg",
}))``;

const DiamondRectangle = styled.path.attrs(() => ({
  points: "M100 100 H 90 V 90 H 10 Z",
  fill: "red",
  stroke: "blue",
}))``;

const Text = styled.text.attrs((props) => ({
  x: props.x || "50%",
  y: props.y || "50%",
  fill: props.fill || "white",
}))`
  font-weight: Bold;
  font-size: 3rem;
  letter-spacing: 4px;
`;
